class dataSet {
	static router = null;
	static setRouter (router) {
		this.router = router
	}
	static get router () {
		if (typeof this.router != 'undefined') {
			return this.router
		} else {
			return null
		}
	}
	/**
	 * 解析响应数据
	 * @param {object} response 数据响应
	 */
	static analysis(response) {
		if (response.hasErr && response.hasErr == 401) {
			if (this.router) {
				this.router.push({
					path: '/login'
				})
			}
		}
	}
}
export default dataSet