import Vue from 'vue'
import Router from 'vue-router'
import None from '@/views/None.vue'
import Home from '@/views/Home.vue'
import Login from '@/views/Login.vue'
import Complete from '@/views/Complete.vue'
import Abandon from '@/views/Abandon.vue'
Vue.use(Router)

export default new Router({
	// mode: 'history',
	mode: 'hash',
	base: process.env.BASE_URL,
	routes: [
		{ // 登录
			path: '/login',
			name: 'login',
			component: Login
		},
		{ // 跟单中
			path: '/',
			name: 'home',
			component: Home
		},
		{ // 跟单中
			path: '/index',
			name: 'home',
			component: Home
		},
		{ // 已成单
			path: '/complete',
			name: 'complete',
			component: Complete
		},
		{ // 已放弃
			path: '/abandon',
			name: 'abandon',
			component: Abandon
		},
		{
			path: '*',
			name: '404',
			component: None
		},
	]
})
