import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import createPersistedState from 'vuex-persistedstate'

export default new Vuex.Store({
	plugins: [createPersistedState({storage: window.localStorage})],
	state: {
		token: '', // 请求的Token
		navigationActive: 0, // 导航下标
		showSearch: false,   // 显示搜索框
	},
	mutations: {
		setToken(state, data) {
			state.token = data
		},
		setNavigationActive(state, data) {
			state.navigationActive = parseInt(data)
		},
		setShowSearch(state, data) {
			state.showSearch = Boolean(data)
		}
	},
	actions: {
		setToken({state, commit}, data) {
			commit('setToken', data)
		}
	}
})
