import router from '@/router'
import store from '@/store'
import requester from 'axios'
import dataSet from '@/network/dataSet'
import Request from '@/network/Request'
Request.config.onUploadProgress = Request.onUploadProgress
Request.config.onDownloadProgress = Request.onDownloadProgress
Request.config.headers = {authorization: store.state.token}
const axios = requester.create(
	Request.config
)
dataSet.setRouter(router);
axios.interceptors.request.use(config => {
	return config;
}, error => {
	return Promise.reject(error);
})

axios.interceptors.response.use(response => {
	store.commit('setToken', response.headers.token)
	if (response.status == 200) {
		dataSet.analysis(response.data)
		return response.data;
	} else {
		return response;
	}
}, error => {
	return Promise.reject(error);
})
export {
	axios,
	store,
	router
}
export default axios