<template>
	<div id="app">
		<Navigation :navigations="navs" v-if="this.$route.name != 'login'">
			<router-link class="logoText" tag="div" to="/" slot="logo"> 跟单系统 </router-link>
			<div slot="dropdown-item">
				<dd>
					<router-link tag="a" to="/profile"> 个人设置 </router-link>
				</dd>
				<dd>
					<a href="javascript:void(0)" @click="signout"> 退出登录 </a>
				</dd>
			</div>
		</Navigation>
		<router-view/>
		<Search v-if="showSearch" />
	</div>
</template>
<script>
import "layui-src/dist/css/layui.css"
import 'element-ui/lib/theme-chalk/index.css'
import Navigation from "@/components/Navigation.vue"
import Search from "@/components/Search.vue"
import { MessageBox, Notification } from "element-ui"
import { mapState } from 'vuex';
export default {
	name: "app",
	components: { Navigation, Search },
	data() {
		return {
			navs: [
				{
					title: "跟单中",
					href: "/index",
					bindShow: {
						admin: 0
					}
				},
				{
					title: "已完成",
					href: "/complete",
					bindShow: {
						admin: 0
					}
				},
				{
					title: "已放弃",
					href: "/abandon",
					bindShow: {
						admin: 0
					}
				},
				{
					title: "统计",
					href: "/statistics",
					bindShow: {
						admin: 0
					}
				},
				{
					title: "用户管理",
					href: "/member",
					bindShow: {
						admin: 1
					}
				},
				{
					title: "部门管理",
					href: "/department",
					bindShow: {
						admin: 1
					}
				},
				{
					title: "支付渠道",
					href: "/department",
					bindShow: {
						admin: 1
					}
				},
			]
		}
	},
	computed: mapState({
		showSearch: 'showSearch'
	}),
	mounted() {
		document.onkeydown = event => {
			var keyCode = event.keyCode || event.which || event.charCode;
			var ctrlKey = event.ctrlKey || event.metaKey;
			if (keyCode == 27) {
				this.$store.commit('setShowSearch', false);
			}
			if (ctrlKey && keyCode == 13) {
				event.preventDefault();
				event.stopPropagation();
				this.$store.commit('setShowSearch', true);
			}
		}
	},
	methods: {
		signout() {
			MessageBox.confirm("您确定要退出？", "退出提示", {
				confirmButtonText: "确定",
				cancelButtonText: '放弃',
				type: 'warning'
			}).then(() => {
				this.$store.commit('setToken', null)
				this.$router.push({
					path: '/login'
				})
				Notification.success({
					title: "成功",
					message: "您已经成功退出"
				})
			}).catch(() => {
				
			})
		}
	}
}
</script>
<style lang="scss">
body {
	padding: 0;
	margin: 0;
	background-color: #eee;
	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		color: #2c3e50;
		.logoText {
			font-size: 18px;
			font-weight: bold;
			color: #fff;
			cursor: pointer;
		}
	}
}
</style>
