<template>
	<div id="navigation" class="layui-layout layui-layout-admin">
		<div class="layui-header">
			<div class="layui-logo">
				<slot name="logo"></slot>
			</div>
			<ul class="layui-nav layui-layout-left">
				<li v-if="navigations" v-for="(navigation, index) in navigations" @click="activeIndex(index)" :class="{'layui-nav-item': true, 'layui-this': (index == active)}" :key="index">
					<router-link :to="navigation.href">{{navigation.title}}</router-link>
				</li>
			</ul>
			<ul class="layui-nav layui-layout-right">
				<li class="layui-nav-item" @click="showSearch">
					<i class="el-icon-search"></i>
				</li>
				<li class="layui-nav-item" @click.stop="Dropdown">
					<a href="javascript:;">
						<img src="http://t.cn/RCzsdCq" class="layui-nav-img">
						贤心
					</a>
					<dl class="layui-nav-child" v-show="dropdown">
						<slot name="dropdown-item"></slot>
					</dl>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
import { setTimeout } from 'timers'
import { mapState } from 'vuex'
export default {
	name: "navigation",
	props: {
		navigations: {
			type: Array,
			default() {
				return {}
			}
		}
	},
	computed: mapState({
		active: 'navigationActive'
	}),
	data() {
		return {
			dropdown: false,
		}
	},
	mounted() {
		document.addEventListener("click", event => {
			this.dropdown = false;
		})
	},
	methods: {
		showSearch() {
			this.$store.commit('setShowSearch', true);
		},
		activeIndex(index) {
			this.$store.commit('setNavigationActive', index)
		},
		Dropdown() {
			this.dropdown = !this.dropdown;
		}
	}
}
</script>

<style lang="scss" scoped>
#navigation {
	.layui-logo {
		background-color: #393D49;
	}
	.layui-nav-item {
		.el-icon-search {
			display: block;
			font-size: 24px;
			height: 60px;
			width: 60px;
			line-height: 56px;
			text-align: center;
			cursor: pointer;
			&:hover {
				background-color: #393D49;
			}
		}
	}
	.layui-nav-child {
		display: block;
	}
}
</style>