		},
		/**
		 * 点击修改支付渠道
		 * @param int index 支付渠道下标
		 */
		changeDitch(index) {
			var ditch = "微信";
			MessageBox.confirm(`您确定要将支付渠道修改为 ${ditch} ？`, "请确认", {
				confirmButtonText: "确定",
				cancelButtonText: "不要",
				type: 'warning'
			}).then(() => {
				Message({
					message: "修改支付渠道成功",
					type: "success"
				})
			}).catch(() => {

			}) 
		},
		changeEnter() {
			MessageBox.confirm(`您确定已经录入易途 ？`, "请确认", {
				confirmButtonText: "确定",
				cancelButtonText: "没有",
				type: 'warning'
			}).then(() => {
				Message({
					message: "修改易途录入状态成功",
					type: "success"
				})
			}).catch(() => {

			}) 
		},
		/**
		 * 完成订单
		 */
		calcOrder() {
			MessageBox.confirm(`您确定此订单已经完成？`, "请确认", {
				confirmButtonText: "确定",
				cancelButtonText: "没有",
				type: 'warning'
			}).then(() => {
				const loading = Loading.service({
					lock: true,
					text: '正在核算数据',
					background: 'rgba(255, 255, 255, 0.7)'
				});
				setTimeout(() => {
					loading.close();
					Message({
						message: "恭喜您又成功做成一笔交易，记得请我吃冰淇淋哦",
						type: "success"
					})
				}, 800)
			}).catch(() => {

			})
		},
		closeOrder () {
			MessageBox.prompt("","您确认要放弃此订单？", {
				confirmButtonText: "确定",
				cancelButtonText: "不放弃",
				inputPlaceholder: "放弃我也请给我个理由吧！",
				inputPattern: /.+/,
				inputErrorMessage: '理由!!! 给我个理由😭 !!!',
			}).then(() => {
				const loading = Loading.service({
					lock: true,
					text: '正在关闭订单',
					background: 'rgba(255, 255, 255, 0.7)'
				});
				setTimeout(() => {
					loading.close();
					Message({
						message: "没事客户总是会有的，不要泄气哦！",
						type: "info"
					})
				}, 800)
			}).catch(() => {

			})
		}
	}
